import './index.scss'
import targetProperties from './targetProperties.js'
import JSZip from 'jszip'
import Pica from 'pica'
import { saveAs } from 'file-saver'

const pica = new Pica()
const transitionDuration = 750
const greenColor = 'rgb(76, 217, 100)'
const blueColor = 'rgb(0, 122, 255)'

const uploadZone = document.getElementById('upload-zone')
const uploadZoneTitle = document.getElementById('upload-zone-title')
const uploadZoneSubtitle = document.getElementById('upload-zone-subtitle')
const actionButton = document.getElementById('action-button')
const actionButtonBackground = document.getElementById('action-button-background')
const actionButtonText = document.getElementById('action-button-text')
const loadingOverlay = document.getElementById('loading-overlay')

const state = {
    originalActionButtonText: actionButtonText.innerText,
    image: null,
    iconsBlob: null,
    options: {
        targets: {
            'ios-marketing': true
        },
        other: {}
    }
}

Array.from(document.getElementsByTagName('fieldset')).forEach(fieldset =>
    Array.from(fieldset.querySelectorAll('input[type="checkbox"]')).forEach(checkbox => {
        state.options[fieldset.name][checkbox.name] = checkbox.checked
        checkbox.addEventListener('change', () => {
            state.options[fieldset.name][checkbox.name] = checkbox.checked
        })
    })
)

const generateIcons = async () => {
    const targets = Object.entries(state.options.targets)
        .map(([target, enabled]) => enabled && target)
        .filter(Boolean)
    if (targets.length <= 1) return

    loadingOverlay.style.display = 'block'
    await new Promise(resolve => {
        actionButtonBackground.style.transition = `width ${transitionDuration}ms`
        actionButtonBackground.style.width = '0'
        setTimeout(resolve, transitionDuration)
    })
    actionButtonBackground.style.transition = 'none'
    actionButtonText.innerText = ''

    const icons = []
    targets.forEach(target => Object.entries(targetProperties[target]).forEach(([points, scales]) =>
        scales.forEach(scale => icons.push({
            pixels: Number(points) * scale,
            content: {
                size: `${points}x${points}`,
                idiom: target,
                scale: `${scale}x`,
                filename: `AppIcon-${points}@${scale}x~${target}.png`
            }
        }))
    ))

    const reader = new FileReader()
    reader.onload = e => {
        const zip = new JSZip()
        const img = new Image()
        let resizeCount = 0
        img.onload = async () => {
            const originalCanvas = document.createElement('canvas')
            originalCanvas.width = img.width
            originalCanvas.height = img.height
            originalCanvas.getContext('2d').drawImage(img, 0, 0)

            for (const icon of icons) {
                const targetCanvas = document.createElement('canvas')
                targetCanvas.width = icon.pixels
                targetCanvas.height = icon.pixels

                const result = await pica.resize(originalCanvas, targetCanvas)
                resizeCount++
                actionButtonBackground.style.width = `${100 * resizeCount / icons.length}%`
                const blob = await pica.toBlob(result, 'image/png')
                zip.file(icon.content.filename, blob)

                if (Object.keys(zip.files).length === icons.length) {
                    zip.file('Contents.json', JSON.stringify({
                        images: icons.map(icon => icon.content),
                        info: {
                            version: 1,
                            author: 'Icon Maker'
                        }
                    }))
                    state.iconsBlob = await zip.generateAsync({type: 'blob'})
                    actionButton.style.borderColor = greenColor
                    actionButtonBackground.style.background = greenColor
                    actionButtonText.innerText = 'Click to download icons'
                    loadingOverlay.style.display = 'none'
                    if (state.options.other['automatic-download']) downloadIcons(state.iconsBlob)
                }
            }
        }
        img.src = e.target.result
    }
    reader.readAsDataURL(state.image)
}

const handleActionButtonClick = () => {
    if (state.iconsBlob) {
        downloadIcons(state.iconsBlob)
    } else if (state.image) generateIcons()
}

const handleFileUpload = e => {
    e.preventDefault()
    const target = e.type === 'change' ? e.target.parentElement : e.target
    if (target !== uploadZone) return

    removeDragClass()
    const file = (e.target.files || e.dataTransfer.files)[0]
    if (file && file.type.includes('image')) {
        uploadZoneTitle.innerText = file.name
        uploadZoneSubtitle.innerText = 'Drop icon here or click to upload another one'
        actionButtonText.innerText = state.originalActionButtonText
        actionButtonBackground.style.background = blueColor
        actionButton.style.borderColor = blueColor
        actionButton.disabled = false
        state.iconsBlob = null
        state.image = file
    }
}

const handleDragOver = e => {
    e.preventDefault()
    if (e.target === uploadZone) e.dataTransfer.dropEffect = 'copy'
}

const downloadIcons = iconsBlob => saveAs(iconsBlob, 'AppIcon.appiconset.zip')
const addDragClass = () => uploadZone.classList.add('drag')
const removeDragClass = () => uploadZone.classList.remove('drag')

actionButton.addEventListener('click', handleActionButtonClick)
uploadZone.addEventListener('dragenter', addDragClass)
uploadZone.addEventListener('dragleave', removeDragClass)
uploadZone.addEventListener('change', handleFileUpload)
window.addEventListener('drop', handleFileUpload)
window.addEventListener('dragover', handleDragOver)
